if (Meteor.isServer) {
  Meteor.publish('coll', function(name, selector, options) {
    return coll[name].find(selector, options);
  });
  Meteor.methods({
    "import": function(name, selector, modifier) {
      coll[name].upsert(selector, {
        $set: modifier
      });
      return _.map(['wilSum', 'wilStat'], function(i) {
        return Meteor.call(i);
      });
    },
    empty: function(name, selector) {
      return coll[name].remove(selector);
    },
    update: function(name, obj) {
      return coll[name].update(obj._id, {
        $set: obj
      });
    },
    wilSum: function() {
      var sumit, years;
      years = _.map([2015, 2016, 2017, 2018, 2019], function(i) {
        return 'y' + i;
      });
      sumit = function(wil, filter) {
        var i, j, l, len, len1, m, modifier, results, selector, source, sumer, uniqBy;
        source = coll.elemens.find(filter).fetch();
        uniqBy = _.uniqBy(source, function(i) {
          var arr;
          arr = [i.elemen, i.indikator];
          if (wil === 'kec') {
            arr.push(i.kab, i.kec);
          } else if (wil === 'kab') {
            arr.push(i.kab);
          }
          return _.join(arr);
        });
        results = [];
        for (l = 0, len = uniqBy.length; l < len; l++) {
          i = uniqBy[l];
          selector = {
            elemen: i.elemen,
            indikator: i.indikator
          };
          sumer = {
            elemen: i.elemen,
            indikator: i.indikator
          };
          if (wil === 'kec') {
            selector.kab = i.kab;
            selector.kec = i.kec;
            selector.kel = '*';
            sumer.kab = i.kab;
            sumer.kec = i.kec;
            sumer.kel = {
              $ne: '*'
            };
          } else if (wil === 'kab') {
            selector.kab = i.kab;
            selector.kec = '*';
            selector.kel = '*';
            sumer.kab = i.kab;
            sumer.kec = {
              $ne: '*'
            };
            sumer.kel = '*';
          } else if (wil === 'riau') {
            selector.kab = '*';
            selector.kec = '*';
            selector.kel = '*';
            sumer.kab = {
              $ne: '*'
            };
            sumer.kec = '*';
            sumer.kel = '*';
          }
          modifier = {};
          for (m = 0, len1 = years.length; m < len1; m++) {
            j = years[m];
            i[j].rel = _.sumBy(coll.elemens.find(sumer).fetch(), function(k) {
              return k[j].rel;
            });
            modifier[j] = i[j];
          }
          results.push(coll.elemens.upsert(selector, {
            $set: modifier
          }));
        }
        return results;
      };
      sumit('kec', {
        kab: {
          $ne: '*'
        },
        kec: {
          $ne: '*'
        },
        kel: {
          $ne: '*'
        }
      });
      sumit('kab', {
        kab: {
          $ne: '*'
        },
        kec: {
          $ne: '*'
        },
        kel: '*'
      });
      return sumit('riau', {
        kab: {
          $ne: '*'
        },
        kec: '*',
        kel: '*'
      });
    },
    wilStat: function() {
      var state, years;
      state = function(selector, years, kab, kec, kel) {
        var find, i, j, l, len, len1, len2, len3, m, maped, modifier, n, o, results, stats;
        stats = [];
        maped = _.map(coll.elemens.find(selector).fetch(), function(i) {
          var j, l, len;
          i.indikator = 0;
          for (l = 0, len = years.length; l < len; l++) {
            j = years[l];
            i[j].kin = i[j].rel / i[j].tar;
            i[j].sumKin = 0;
          }
          return i;
        });
        for (l = 0, len = maped.length; l < len; l++) {
          i = maped[l];
          find = _.find(stats, function(j) {
            var elem;
            elem = function() {
              return j.elemen === i.elemen;
            };
            kab = function() {
              return j.kab === i.kab;
            };
            kec = function() {
              return j.kec === i.kec;
            };
            kel = function() {
              return j.kel === i.kel;
            };
            if (kel) {
              return elem() && kab() && kec() && kel();
            } else if (kec) {
              return elem() && kab() && kec();
            } else if (kab) {
              return elem() && kab();
            } else {
              return elem;
            }
          });
          if (find) {
            find.indikator += 1;
            for (m = 0, len1 = years.length; m < len1; m++) {
              j = years[m];
              find[j].sumKin += i[j].kin;
              find[j].avgKin = find[j].sumKin / find.indikator;
            }
          } else {
            stats.push(i);
          }
        }
        results = [];
        for (n = 0, len2 = stats.length; n < len2; n++) {
          i = stats[n];
          selector = {
            elemen: i.elemen
          };
          if (kab) {
            selector.kab = i.kab;
          }
          if (kec) {
            selector.kec = i.kec;
          }
          if (kel) {
            selector.kel = i.kel;
          }
          modifier = {
            indikator: i.indikator
          };
          for (o = 0, len3 = years.length; o < len3; o++) {
            j = years[o];
            modifier[j] = {
              sumKin: i[j].sumKin,
              avgKin: i[j].avgKin
            };
          }
          results.push(coll.wilStat.upsert(selector, {
            $set: modifier
          }));
        }
        return results;
      };
      years = _.map([2015, 2016, 2017, 2018, 2019], function(i) {
        return 'y' + i;
      });
      state({
        kel: {
          $ne: '*'
        }
      }, years, true, true, true);
      state({
        kec: {
          $ne: '*'
        },
        kel: '*'
      }, years, true, true);
      state({
        kab: {
          $ne: '*'
        },
        kec: '*',
        kel: '*'
      }, years, true);
      return state({
        kab: '*',
        kec: '*',
        kel: '*'
      }, years);
    }
  });
}
