var i, j, k, key, l, len, len1, len2, len3, m, makeFasil, makeWil, n, obj, objek, ref, ref1, ref2, ref3, val, years,
  slice = [].slice;

Router.configure({
  layoutTemplate: 'layout',
  loadingTemplate: 'loading'
});

Router.route('/', {
  action: function() {
    return this.render('home');
  }
});

Router.route('/login', {
  action: function() {
    return this.render('login');
  }
});

Router.route('/logout', {
  action: function() {
    return [Meteor.logout(), Router.go('/')];
  }
});

Router.route('/riau', {
  action: function() {
    return this.render('wil');
  }
});

Router.route('/ikd', {
  action: function() {
    return this.render('ikd');
  },
  waitOn: function() {
    return Meteor.subscribe('coll', 'ikd', {}, {});
  }
});

Router.route('/jalan', {
  action: function() {
    return this.render('jalan');
  },
  waitOn: function() {
    return [Meteor.subscribe('coll', 'jalProv', {}, {}), Meteor.subscribe('coll', 'jalNas', {}, {})];
  }
});

makeWil = function(route) {
  return Router.route('/' + route, {
    action: function() {
      return this.render('wil');
    }
  });
};

ref = slice.call(kels).concat(slice.call(kecs), slice.call(kabs));
for (j = 0, len = ref.length; j < len; j++) {
  i = ref[j];
  makeWil(i);
}

makeFasil = function(name) {
  return Router.route('/' + name, {
    action: function() {
      return this.render('fasilitas');
    },
    waitOn: function() {
      var options, selector;
      selector = {
        kelompok: name
      };
      options = {};
      return Meteor.subscribe('coll', 'fasilitas', selector, options);
    }
  });
};

for (key in headings) {
  val = headings[key];
  makeFasil(key);
}

years = function(start, end) {
  var k, results;
  return _.map((function() {
    results = [];
    for (var k = start; start <= end ? k <= end : k >= end; start <= end ? k++ : k--){ results.push(k); }
    return results;
  }).apply(this), function(i) {
    return 'y' + i;
  });
};

coll.elemens = new Meteor.Collection('elemens');

objek = {
  kab: {
    type: String,
    autoform: {
      type: 'hidden'
    }
  },
  kec: {
    type: String,
    autoform: {
      type: 'hidden'
    }
  },
  kel: {
    type: String,
    autoform: {
      type: 'hidden'
    }
  },
  elemen: {
    type: String,
    autoform: {
      type: 'hidden'
    }
  },
  indikator: {
    type: String,
    autoform: {
      disabled: true
    }
  },
  defenisi: {
    type: String,
    optional: true,
    autoform: {
      disabled: true
    }
  },
  satuan: {
    type: String,
    optional: true
  }
};

ref1 = years(2015, 2019);
for (k = 0, len1 = ref1.length; k < len1; k++) {
  i = ref1[k];
  objek[i] = {
    type: Object,
    optional: true
  };
  objek[i + '.tar'] = {
    type: Number,
    decimal: true
  };
  objek[i + '.rel'] = {
    type: Number,
    decimal: true
  };
}

coll.elemens.attachSchema(new SimpleSchema(objek));

coll.elemens.allow({
  insert: function() {
    return true;
  },
  update: function() {
    return true;
  },
  remove: function() {
    return true;
  }
});

